/**
 * Configuration/builder to build an table config
 * This object contains configuration regarding initial sorting of an object
 */
import { Type } from '@angular/core';
import { TableItemComponent } from '@sofico-framework/ui-kit/components/table-list-item';
import { SortingOrderConfig } from '@sofico-framework/utils';

export class TableListConfig<T> {
  /**
   * FunctionalProps:
   * id: Used as a unique identifier for sorting as we can't assume the translations in column headers are uniaue.
   */
  functionalProps: {
    id: number;
    header: string;
    selector: (entity: T) => any;
    searchable: boolean;
    sortable: boolean;
    plainSort: boolean;
  }[] = [];
  initialSorting: SortingOrderConfig<T> = {
    prop: null
  };
  dynamicRowComponent: Type<TableItemComponent<T>> = null;
  initialSortingFuncPropRef = null;
  private functionalPropCount = 0;

  setDynamicRowComponent(
    component: Type<TableItemComponent<T>>
  ): TableListConfig<T> {
    this.dynamicRowComponent = component;
    return this;
  }

  withNaturalSorting(): TableListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];

    propToUpdate.sortable = true;
    propToUpdate.plainSort = false;
    if (this.initialSortingFuncPropRef) {
      this.initialSortingFuncPropRef.plainSort = propToUpdate.plainSort;
    }
    return this;
  }

  withPlainSorting(label: string): TableListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    propToUpdate.sortable = true;
    propToUpdate.plainSort = true;
    if (this.initialSortingFuncPropRef) {
      this.initialSortingFuncPropRef.plainSort = propToUpdate.plainSort;
    }
    return this;
  }

  asInitialSorting(order: 'asc' | 'desc'): TableListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    this.initialSorting = {
      prop: propToUpdate.selector,
      order,
      plainSort: propToUpdate.plainSort
    } as SortingOrderConfig<T>;
    this.initialSortingFuncPropRef = propToUpdate;
    return this;
  }

  addFunctionalProp(selector: (entity: T) => any): TableListConfig<T> {
    this.functionalProps.push({
      id: this.functionalPropCount,
      header: '',
      selector,
      searchable: false,
      sortable: false,
      plainSort: false
    });
    this.functionalPropCount++;
    return this;
  }

  setHeader(header: string): TableListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    propToUpdate.header = header;
    return this;
  }

  withSearch(): TableListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    propToUpdate.searchable = true;
    return this;
  }
}
