import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { TableListSearchBarComponent } from '@sofico-framework/ui-kit/components/table-list-search-bar';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';
import { search, SortingOrderConfig, sortList } from '@sofico-framework/utils';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import {
  BehaviorSubject,
  combineLatest,
  Observable,
  of,
  ReplaySubject
} from 'rxjs';
import { map, shareReplay, switchMap } from 'rxjs/operators';
import { TableListConfig } from './classes/table-list-config.class';

@UntilDestroy()
@Component({
  selector: 'sof-table-list',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <ng-container *ngIf="config$ | async as config">
      <div *ngIf="enableSearch" class="w-100">
        <sof-table-list-search-bar
          #searchBarComponent
          [tc]="tc"
          (changeTerm)="onChangeTerm($event)"
        ></sof-table-list-search-bar>
      </div>
      <ng-container *ngIf="hasSearchedEntities | async; else noResults">
        <div class="table-responsive">
          <table class="table table-borderless table-striped">
            <thead>
              <tr>
                <th
                  *ngFor="let prop of config.functionalProps; let i = index"
                  (click)="onChangeSorting(prop.id, i)"
                >
                  <div class="d-flex">
                    <div>{{ tc + '.' + prop.header | translate }}</div>
                    <sof-svg-icon
                      class="ml-1"
                      [class.visibility-hidden]="activeSortPropId !== prop.id"
                      [icon]="
                        (sortingSub$ | async)?.order === 'asc'
                          ? 'icon-sort-amount-asc'
                          : 'icon-sort-amount-desc'
                      "
                      size="12"
                    ></sof-svg-icon>
                  </div>
                </th>
              </tr>
            </thead>
            <tbody>
              <sof-table-list-item
                *ngFor="
                  let entity of searchedEntities$
                    | async
                    | sofSort: (sortingSub$ | async);
                  trackBy: trackByFn
                "
                [entity]="entity"
                [tc]="tc"
                [dynamicRowComponent]="config?.dynamicRowComponent"
              >
              </sof-table-list-item>
            </tbody>
          </table>
        </div>
      </ng-container>
      <ng-template #noResults>
        <ng-container *ngIf="entities$ | async as entities">
          <ng-container *ngIf="entities?.length > 0; else noData">
            {{ tc + '.' + 'SEARCH-NO-RESULTS' | translate }}
          </ng-container>
          <ng-template #noData>
            {{ tc + '.' + 'SEARCH-NO-DATA' | translate }}
          </ng-template>
        </ng-container>
      </ng-template>
    </ng-container>
  `,
  styleUrls: ['./table-list.component.scss'],
  providers: [{ provide: SOF_FOCUS_COMPONENT, useExisting: TableListComponent }]
})
export class TableListComponent<T> implements OnInit, OnDestroy, OnSofFocus {
  entities$ = new ReplaySubject<T[]>(1);
  config$ = new ReplaySubject<TableListConfig<T>>(1);
  searchableSelectors: ((entity: T) => any)[];

  /**
   * The amount of items shown in the table.
   */
  @Input() thresholdNumberOfItems = 15;

  /**
   * The translation context.
   */
  @Input() tc: string;

  /**
   * The table list config.
   */
  @Input() set config(config: TableListConfig<T>) {
    if (config) {
      this.config$.next(config);
      this.sortingSub$.next(config.initialSorting);
      this.searchableSelectors = config.functionalProps
        .filter(prop => prop.searchable)
        .map(prop => prop.selector);
      this.selectors = config?.functionalProps
        ?.filter(prop => prop.sortable)
        ?.map(prop => prop?.selector);
      this.plainSorts = config?.functionalProps
        ?.filter(prop => prop.sortable)
        ?.map(prop => prop?.plainSort);

      if (config.initialSorting) {
        this.activeSortPropId = config.initialSortingFuncPropRef.id;
      }
    }
  }

  /**
   * The entities we want to render in this table.
   */
  @Input() set entities(entities: T[]) {
    if (entities) {
      this.entities$.next(entities);
    }
  }

  /**
   * Whether the search bar is visible.
   */
  @Input() enableSearch = true;

  /**
   * Whether the sorting dropdown is visible.
   */
  @Input() enableSorting = true;

  @Output() searchedEntities = new EventEmitter<T[]>();

  @ViewChild('searchBarComponent')
  searchBarComponent: TableListSearchBarComponent<T>;

  // presentation streams
  searchedEntities$: Observable<T[]>;
  sortedEntities$: Observable<T[]>;
  hasSearchedEntities: Observable<boolean>;

  // source streams
  termSub$ = new BehaviorSubject<string>('');
  sortingSub$ = new BehaviorSubject<SortingOrderConfig<T>>(null);
  private numberOfItemsToDisplaySub$ = new BehaviorSubject<number>(
    this.thresholdNumberOfItems
  );

  selectors: ((entity: T) => any)[];
  plainSorts: boolean[];

  activeSortPropId: number;

  trackByFn = i => i;

  sofFocus(): void {
    if (this.enableSearch) {
      this.searchBarComponent.sofFocus();
    }
  }

  ngOnInit(): void {
    this.searchedEntities$ = this.getSearchedEntities$();
    this.searchedEntities$
      .pipe(takeUntilDestroy(this))
      .subscribe(searchedEntities =>
        this.searchedEntities.emit(searchedEntities)
      );
    this.sortedEntities$ = this.getSortedEntities$();

    this.hasSearchedEntities = this.getHasEntities$();
  }

  ngOnDestroy(): void {}

  onChangeSorting(id: number, index: number): void {
    this.sortingSub$.next({
      prop: this.selectors[index],
      plainSort: this.plainSorts[index],
      order:
        id === this.activeSortPropId
          ? this.sortingSub$.getValue().order === 'asc'
            ? 'desc'
            : 'asc'
          : 'asc'
    });

    this.activeSortPropId = id;
  }

  onChangeTerm(term: string): void {
    this.termSub$.next(term);
    this.numberOfItemsToDisplaySub$.next(this.thresholdNumberOfItems);
    this.getSearchedEntities$();
  }

  private getSearchedEntities$(): Observable<T[]> {
    return this.entities$.pipe(
      switchMap(results =>
        this.enableSearch && this.searchableSelectors.length !== 0
          ? of(results).pipe(search(this.termSub$, this.searchableSelectors))
          : of(results)
      ),
      shareReplay({ refCount: true, bufferSize: 1 })
    );
  }

  private getSortedEntities$(): Observable<T[]> {
    return combineLatest([this.entities$, this.sortingSub$]).pipe(
      map(([entities, sorting]) => sortList(entities, sorting))
    );
  }

  private getHasEntities$(): Observable<boolean> {
    return this.searchedEntities$.pipe(
      map(entities => entities?.length > 0),
      shareReplay({ refCount: true, bufferSize: 1 })
    );
  }
}
